.imgCover{
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}
    
.imgBgBottom {
  background-position: bottom;
  background-size: cover;
  background-repeat: no-repeat;
}


//center
.center{
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
}

//logo
.logo {
  display:block; /*a*/
  background: url('../images/logo/logo1-sm.png') no-repeat; /**/
  width: 132px; /**/
  height: 30px; /**/
  text-indent: 101%; /**/
  overflow: hidden; /**/
  white-space: nowrap; /*""*/
  background-size: cover;
}

//googleMap
.googleMap {
  overflow: hidden;
   iframe{
       width: 100%;
       height: 400px;
      display:block;
  }
}

// loader
.loadPage{
  min-width: 100%;
  min-height: 100%;
  background-color: $white;
  opacity:0.7;
  transition:  3s ease-in-out; 
}
.loadPage,.loader {
	position: fixed;
	top: 50%;
	left: 50%;
  transform: translateX(-50%) translateY(-50%);
	z-index: 10 !important;
}
.ball-beat > div {
  background-color: $primary;
}
